import React, { useCallback, useMemo, useState } from 'react';
import {
  Box, Button, FormControl, FormLabel, Input
} from '@chakra-ui/react';
import { shallowEqual } from 'react-redux';
import { useTranslation } from 'react-i18next';
import { useNavigate } from 'react-router-dom';
import { useAppAsyncDispatch, useAppSelector } from '../../../hooks/store';
import MiniBox from '../../../layout/MiniBox';
import AddressSearch from '../../../components/AddressSearch';
import { useModuleData, useModulePageData } from '../../../hooks/module';
import { updateContact } from '../../../store/middlewares/contact';
import { apiErrorHandler } from '../../../utils/general';
import { checkPay } from '../../../store/middlewares/payment';
import { sendMessage } from '../../../utils/sdk';
import { Data } from '../../../modules/Payments';
import { CheckData } from '../../../modules/Payments/Check';
import { COMMON } from '../../../const/messages';
import CurrencyInput from '../../../components/CurrencyInput';

const Check:React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useAppAsyncDispatch();
  const navigate = useNavigate();

  const {
    startScreen = 'options',
    gmapKey = ''
  } = useModuleData<Data<'check'>>();

  const { showBack } = useModulePageData<CheckData, Data<'check'>['startScreen']>('check');

  const contactDetails = useAppSelector((store) => store.contact.selectedContact, shallowEqual);
  const account = useAppSelector((store) => store.account.selectedAccount, shallowEqual);
  const [localContactDetails, updateContactData] = useState({
    name: contactDetails?.name || '',
    address: contactDetails?.check?.address || {
      line1: '',
      line2: '',
      city: '',
      state: '',
      country: 'US',
      postalCode: '',
      addressType: 'legal',
    }
  });
  const [paymentData, updatePaymentData] = useState({
    amount: '',
    description: '',
  });

  const isValid = useMemo(() => (
    localContactDetails
      && !!localContactDetails.address
      && !!localContactDetails.address.line1
      && !!localContactDetails.address.city
      && !!localContactDetails.address.state
      && !!localContactDetails.address.country
      && !!localContactDetails.address.postalCode
      && localContactDetails.address.line1.length !== 0
      && localContactDetails.address.city.length !== 0
      && localContactDetails.address.state.length !== 0
      && localContactDetails.address.country.length !== 0
      && localContactDetails.address.postalCode.length !== 0
      && paymentData.amount.length !== 0
      && paymentData.description.length !== 0
  ), [localContactDetails, paymentData]);

  const submit = useCallback(() => {
    if (isValid && contactDetails && contactDetails.id && localContactDetails.address && account) {
      dispatch(updateContact(contactDetails.id, {
        accountId: contactDetails.accountId,
        check: {
          address: localContactDetails.address
        }
      })).then(() => {
        dispatch(checkPay({
          accountId: account.id,
          contactId: contactDetails.id || '',
          type: 'physical',
          amount: paymentData.amount,
          description: paymentData.description
        })).then(() => {
          navigate('/payments/success');
        }).catch(apiErrorHandler);
      }).catch(apiErrorHandler);
    }
  }, [localContactDetails, contactDetails, isValid, account, paymentData]);

  return (
    <div className="page">
      <form onSubmit={(e) => {
        e.preventDefault();
        submit();
      }}
      >
        <MiniBox
          heading={t('payments_check.title')}
          footer={(
            <Button width="100%" variant="primary" disabled={!isValid} onClick={submit}>
              {t('payments_check.ctaText')}
            </Button>
          )}
          showBack={startScreen !== 'check' || !!showBack}
          onBack={() => {
            sendMessage(COMMON.BACK_PRESSED, { from: 'check' });
            if (startScreen !== 'check') {
              navigate(-1);
            }
          }}
        >
          <Box width="100%" paddingTop="15px">

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_check.name')}
              </FormLabel>
              <Input
                variant="primary"
                value={localContactDetails?.name || ''}
                onChange={(e) => {
                  updateContactData({
                    ...localContactDetails,
                    name: e.target.value
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_check.address1')}
              </FormLabel>
              <AddressSearch
                restrictedCountry="US"
                placeholder={t('payments_check.address1')}
                googleApiKey={gmapKey}
                address={localContactDetails?.address || undefined}
                onChange={(address) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      ...address,
                      addressType: 'legal',
                    }
                  });
                }}
              />
              {/* <Input variant="primary" value="" /> */}
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_check.address2')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('payments_check.address2')}
                value={localContactDetails?.address?.line2}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      line2: e.target.value,
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_check.addressCity')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('payments_check.addressCity')}
                value={localContactDetails?.address?.city}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      city: e.target.value,
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_check.addressState')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('payments_check.addressState')}
                value={localContactDetails?.address?.state}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      state: e.target.value,
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_check.addressZipcode')}
              </FormLabel>
              <Input
                variant="primary"
                placeholder={t('payments_check.addressZipcode')}
                value={localContactDetails?.address?.postalCode}
                onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                  updateContactData({
                    ...localContactDetails,
                    address: {
                      ...localContactDetails.address,
                      postalCode: e.target.value,
                    }
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_check.amount')}
              </FormLabel>
              <CurrencyInput
                value={paymentData.amount}
                onChange={(value) => {
                  updatePaymentData({
                    ...paymentData,
                    amount: value || ''
                  });
                }}
              />
            </FormControl>

            <FormControl variant="primary">
              <FormLabel variant="primary">
                {t('payments_check.purpose')}
              </FormLabel>
              <Input
                variant="primary"
                value={paymentData.description}
                onChange={(e) => {
                  updatePaymentData({
                    ...paymentData,
                    description: e.target.value
                  });
                }}
              />
            </FormControl>

          </Box>
        </MiniBox>
      </form>
    </div>
  );
};

export default Check;
